import * as React from "react";
import { useState, useEffect } from 'react';
import BlogCard from "../components/BlogCard";
import { Grid } from "@mui/material";
import axios from "axios";

const Blogs = () => {
  const [blogs, setBlogs] = useState([]);
  // const userId = localStorage.getItem("userId");

  useEffect(() => {
    const getAllBlogs = async () => {
      try {
        const { data } = await axios.get("/api/v1/blog/all-blog");
        if (data?.success) {
          setBlogs(data?.blogs);
        }
      } catch (error) {
        console.error("Error fetching blogs:", error);
      }
    };
    getAllBlogs();
  }, []);

  // Function to truncate description to a certain length
  const truncateDescription = (text, maxLength) => {
    if (text.length <= maxLength) {
      return text;
    }
    const truncatedText = text.slice(0, maxLength) + "...";
    return truncatedText;
  };

  return (
    <Grid container spacing={4} justifyContent="center">
      {blogs.map((blog) => {
        console.log("Blog Object:", blog); // Add this line to log the blog object
        return (
          <Grid item key={blog._id} xs={12} sm={6} md={4} lg={3}>
            <BlogCard
              id={blog._id}
              isUser={localStorage.getItem("userId") === blog?.user?._id}
              title={blog.title}
              description={truncateDescription(blog.description, 150)}
              image={blog.image}
              username={blog?.user?.username}
              time={blog.createdAt}
              comments={blog.comments.length}
              likes={blog.likes.length}
            />
          </Grid>
        );
      })}
    </Grid>
  );

};

export default Blogs;
