import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import BlogCard from "../components/BlogCard"
import Card from "@mui/material/Card"
import { TextField, Button } from "@mui/material";

function SingleBlog() {
    const { id } = useParams();
    const [blog, setBlog] = useState(null);
    const [error, setError] = useState(null);
    const [commentText, setCommentText] = useState('');
    const [comments, setComments] = useState([]);

    useEffect(() => {
        const fetchBlog = async () => {
            try {
                const response = await axios.get(`/api/v1/blog/get-blog/${id}`);
                setBlog(response.data.blog);
                setComments(response.data.blog.comments); // Set comments from fetched blog
            } catch (error) {
                setError(error.message);
            }
        };

        fetchBlog();
    }, [id]);

    const handleAddComment = async () => {
        try {
            const response = await axios.put(`/api/v1/blog/comment/post/${id}`, {
                text: commentText
            });
            const updatedComments = response.data.comments;
            setComments(updatedComments);
            setCommentText('');
        } catch (error) {
            console.error('Error adding comment:', error);
        }
    };

    if (error) {
        return <div>Error: {error}</div>;
    }

    return (
        <Card
            sx={{
                width: "50%",
                margin: "auto",
            }}
        >
            {blog ? (
                <>
                    <BlogCard
                        key={blog._id}
                        id={blog._id}
                        isUser={true}
                        title={blog.title}
                        description={blog.description}
                        image={blog.image}
                        username={blog?.user?.username}
                        time={blog.createdAt}
                        showEditDeleteIcons={false} // Pass this prop to hide edit and delete icons
                    />
                    {/* Comment Section */}
                    <div>
                        <TextField
                            label="Add Comment"
                            variant="outlined"
                            value={commentText}
                            onChange={(e) => setCommentText(e.target.value)}
                        />
                        <Button variant="contained" onClick={handleAddComment}>Add Comment</Button>
                    </div>
                    <div>
                        {comments.map(comment => (
                            <div key={comment._id}>
                                <p>{comment.text}</p>
                                <p>{comment.created}</p>
                            </div>
                        ))}
                    </div>
                </>
            ) : (
                <h1>No blog found</h1>
            )}
        </Card>
    );
};

export default SingleBlog;
