import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from '@mui/material/CardActions';
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import Avatar from "@mui/material/Avatar";
import Typography from "@mui/material/Typography";
import { red } from "@mui/material/colors";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import DeleteIcon from "@mui/icons-material/Delete";
import { Box, IconButton } from "@mui/material";
import { useNavigate } from "react-router-dom";
import CommentIcon from '@mui/icons-material/Comment';
import toast from "react-hot-toast";
import axios from "axios";
import { Link } from 'react-router-dom'
export default function BlogCard({
  title,
  description,
  image,
  username,
  time,
  id,
  comments,
  likes,
  showPosts,
  likesId,
  isUser,
  showEditDeleteIcons = true, // Add a prop to control the visibility of edit and delete icons
}) {
  const navigate = useNavigate();
  const handleEdit = () => {
    navigate(`/blog-details/${id}`);
  };

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`/api/v1/blog/delete-blog/${id}`);
      if (data?.success) {
        alert("Blog Deleted");
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };
  const addLike = async () => {
    try {
      const { data } = await axios.put(`/api/v1/blog/like/post/${id}`)
      // console.log("likes",data.post)
      if (data.success === true) {
        showPosts()
      }
    } catch (error) {
      console.log(error)
      toast.error(error)
    }
  }
  const removeLike = async () => {
    try {
      const { data } = await axios.put(`/api/v1/blog/removelike/post/${id}`);
      // console.log("remove likes", data.post);
      if (data.success === true) {
        showPosts();
      }
    } catch (error) {
      console.log(error.response.data.error);
      toast.error(error.response.data.error)
    }
  }

  return (
    <Card
      sx={{
        width: "100%",
        margin: "auto",
        mt: 2,
        padding: 2,
        boxShadow: "5px 5px 10px #ccc",
        ":hover:": {
          boxShadow: "10px 10px 20px #ccc",
        },
      }}
    >
      {showEditDeleteIcons && isUser && (
        <Box display={"flex"}>
          <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }}>
            <ModeEditIcon color="info" />
          </IconButton>
          <IconButton onClick={handleDelete}>
            <DeleteIcon color="error" />
          </IconButton>
        </Box>
      )}
      <CardHeader
        avatar={
          <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
            {username}
          </Avatar>
        }
        title={username}
        subheader={time}
      />
      <Link to={`/get-blog/${id}`}>
        <CardMedia component="img" height="194" image={image} alt="Paella dish" />
      </Link>
      <CardContent>
        <Typography variant="h6" color="text.secondary">
          Title : {title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Description : {description}
        </Typography>
      </CardContent>
      <CardActions >
        <Box sx={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
          <Box>


            {/* <IconButton aria-label="add to favorites">
              <FavoriteBorderIcon sx={{ color: 'red' }} />
            </IconButton>


            {likes} Like(s)
          </Box>
          <Box>
            {comments}
            <IconButton aria-label="comment">
              <CommentIcon />
            </IconButton> */}
          </Box>
        </Box>

      </CardActions>

    </Card>

  );
}